html {
	font-size: 10px;
}

html,
body {
	height: 100%;
}

body {
	font-family: 'Uni Sans Book';
	font-weight: normal;
	font-style: normal;
	font-size: 1.6rem;
	line-height: 3rem;
	color: #737373;
}

a,
a:visited {
	color: #0087cc;
	-moz-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	-webkit-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}

a:hover,
a:focus {
	color: #ff7b29;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h01,
.h02,
.h03,
.h04,
.h05,
.h06 {
	font-family: Arial, sans-serif;
	color: #252525;
	font-style: normal;
	text-rendering: optimizeLegibility;
	margin-bottom: 2.1rem;
}

h3,
.h03,
h4,
.h04 {
	margin-bottom: 1.8rem;
}

h5,
.h05,
h6,
.h06 {
	font-family: Arial, sans-serif;
	margin-bottom: 1.2rem;
}

h1,
.h01 {
	font-size: 3.1rem;
	line-height: 1.35;
	letter-spacing: -.1rem;
}

h2,
.h02 {
	font-size: 2.4rem;
	line-height: 1.25;
}

h3,
.h03 {
	font-size: 2rem;
	line-height: 1.5;
}

h4,
.h04 {
	font-size: 1.7rem;
	line-height: 1.76;
}

h5,
.h05 {
	font-size: 1.4rem;
	line-height: 1.7;
	text-transform: uppercase;
	letter-spacing: .15rem;
}

h6,
.h06 {
	font-size: 1.3rem;
	line-height: 1.85;
	text-transform: uppercase;
	letter-spacing: .15rem;
}

p img {
	margin: 0;
}

p.lead {
	font-family: 'Uni Sans Book';
	font-weight: normal;
	font-style: normal;
	font-size: 1.8rem;
	line-height: 1.8;
	color: #737373;
}

em,
i,
strong,
b {
	font-size: 1.6rem;
	line-height: 3rem;
	font-style: normal;
	font-weight: normal;
}

em,
i {
	font-family: "merriweather-italic", serif;
}

strong,
b {
	font-family: "merriweather-bold", serif;
}

small {
	font-size: 1.1rem;
	line-height: inherit;
}

blockquote {
	margin: 3rem 0;
	padding-left: 4rem;
	position: relative;
}

blockquote:before {
	content: "\201C";
	font-size: 8rem;
	line-height: 0px;
	margin: 0;
	color: #333;
	font-family: arial, sans-serif;
	position: absolute;
	top: 3rem;
	left: 0;
}

blockquote p {
	font-family: georgia, serif;
	font-style: italic;
	padding: 0;
	font-size: 1.9rem;
	line-height: 1.75;
}

blockquote cite {
	display: block;
	font-size: 1.2rem;
	font-style: normal;
	line-height: 1.65;
}

blockquote cite:before {
	content: "\2014 \0020";
}

blockquote cite a,
blockquote cite a:visited {
	color: #8d8d8d;
	border: none;
}

abbr {
	font-family: "merriweather-bold", serif;
	font-variant: small-caps;
	text-transform: lowercase;
	letter-spacing: .05rem;
	color: #8d8d8d;
}

var,
kbd,
samp,
code,
pre {
	font-family: Consolas, "Andale Mono", Courier, "Courier New", monospace;
}

pre {
	padding: 2.4rem 3rem 3rem;
	background: #F1F1F1;
}

code {
	font-size: 1.4rem;
	margin: 0 .2rem;
	padding: .3rem .6rem;
	white-space: nowrap;
	background: #F1F1F1;
	border: 1px solid #E1E1E1;
	border-radius: 3px;
}

pre>code {
	display: block;
	white-space: pre;
	line-height: 2;
	padding: 0;
	margin: 0;
}

pre.prettyprint>code {
	border: none;
}

del {
	text-decoration: line-through;
}

abbr[title],
dfn[title] {
	border-bottom: 1px dotted;
	cursor: help;
}

mark {
	background: #FFF49B;
	color: #000;
}

hr {
	border: solid #cfcfd5;
	border-width: 1px 0 0;
	clear: both;
	margin: 2.4rem 0 1.5rem;
	height: 0;
}

ol {
	list-style: decimal;
}

ul {
	list-style: disc;
}

li {
	display: list-item;
	list-style-type: none;
}

ol,
ul {
	margin-left: 1.7rem;
}

ul li {
	padding-left: .4rem;
}

ul ul,
ul ol,
ol ol,
ol ul {
	margin: .6rem 0 .6rem 1.7rem;
}

ul.disc li {
	display: list-item;
	list-style: none;
	padding: 0 0 0 .8rem;
	position: relative;
}

dd {
	margin: 0 0 0 2rem;
}

.section-intro {
	max-width: 700px;
	text-align: center;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 5.2rem;
	position: relative;
}

.section-intro h1 {
	font-size: 4.8rem;
	color: #0079C0;
	line-height: 1.25;
	margin-bottom: 1.2rem;
}

.section-intro h5 {
	color: rgba(56, 66, 78, 0.5);
	font-size: 1.6rem;
	line-height: 1.875;
	margin-bottom: 0.3rem;
	letter-spacing: .4rem;
}

.section-intro .lead {
	color: rgba(0, 0, 0, 0.45);
}

.section-intro .with-bottom-line {
	position: relative;
	overflow: hidden;
}

.section-intro .with-bottom-line::after {
	display: block;
	width: 120px;
	height: 3px;
	content: '\0020';
	position: absolute;
	left: 50%;
	bottom: 0;
	margin-left: -60px;
}

header {
	width: 100%;
	position: absolute;
	left: 0;
	top: 24px;
	z-index: 600;
	background: transparent;
	-moz-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	-webkit-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}

header .row {
	width: auto;
	height: 66px;
	position: relative;
}

header .logo {
	margin-top: 2px;
	z-index: 600;
	position: absolute;
	left: 50px;
	top: 50%;
	-webkit-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
}

header .logo a {
	display: block;
	margin: 0;
	padding: 0;
	border: none;
	font: 0/0 a;
	text-shadow: none;
	color: transparent;
	width: 92px;
	height: 63px;
	background: url("./images/logo.png") no-repeat center;
	background-size: 92px 64px;
	-moz-transition: all 0.5s ease-in-out;
	-o-transition: all 0.5s ease-in-out;
	-webkit-transition: all 0.5s ease-in-out;
	-ms-transition: all 0.5s ease-in-out;
	transition: all 0.5s ease-in-out;
}

.sticky {
	position: fixed;
	background: rgba(255, 255, 255, 0.68);
	backdrop-filter: blur(13px);
	top: 0;
}

.sticky .logo a {
	width: 70px;
	height: 50px;
	background-size: 70px 50px;
}

#main-nav-wrap,
.main-navigation {
	margin: 0;
	padding: 0;
}

a.menu-toggle {
	display: none;
}

#main-nav-wrap {
	font-family: Arial, sans-serif;
	font-weight: bold;
	font-size: 11.5px;
	text-transform: uppercase;
	letter-spacing: 1.5px;
	position: absolute;
	top: 0;
	right: 30px;
}

.main-navigation {
	min-height: 66px;
	display: inline-block;
	width: auto;
	text-align: left;
}

.main-navigation li {
	position: relative;
	display: inline-block;
	list-style: none;
	padding: 0;
	margin: 0;
	height: 66px;
	margin-left: 20px;
	text-align: center;
}

.main-navigation li.current a {}

.main-navigation li.current a::after {
	position: absolute;
	left: 50%;
	bottom: 0;
	width: 80%;
	height: 3px;
	-webkit-transform: translateX(-50%);
	-ms-transform: translateX(-50%);
	transform: translateX(-50%);
	background-color: #ED3237;
	content: '\0020';
	display: block;
	-moz-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	-webkit-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}

.main-navigation li.with-sep a::before {
	position: absolute;
	left: 0;
	top: 50%;
	width: 1px;
	height: 20px;
	margin-top: -10px;
	background-color: rgba(255, 255, 255, 0.2);
	content: '\0020';
	display: block;
	-moz-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	-webkit-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}

.main-navigation li a {
	display: block;
	padding: 18px 7px;
	line-height: 30px;
	text-decoration: none;
	text-align: left;
	color: #98999a;
	position: relative;
	-moz-transition: color 0.3s ease-in-out;
	-o-transition: color 0.3s ease-in-out;
	-webkit-transition: color 0.3s ease-in-out;
	-ms-transition: color 0.3s ease-in-out;
	transition: color 0.3s ease-in-out;
}

.main-navigation li a:active {
	background-color: transparent !important;
}

.main-navigation li a:hover {
	color: #000;
}

@media only screen and (max-width:1024px) {
	.intro-content {
		-webkit-transform: translateY(0);
		-ms-transform: translateY(0);
		transform: translateY(0);
	}

	.intro-content h1 {
		font-size: 4.8rem;
		max-width: 700px;
	}

	.intro-content h5 {
		font-size: 1.6rem;
	}

	.intro-content .modal a {
		width: 72px;
		height: 72px;
		margin-bottom: 3rem;
	}
}

@media only screen and (max-width:768px) {
	#intro {
		min-height: 660px;
	}

	.intro-content h1 {
		font-size: 4.2rem;
		max-width: 550px;
	}

	.intro-content h5 {
		font-size: 1.5rem;
		letter-spacing: .15rem;
	}
}

@media only screen and (max-width:300px) {
	#intro {
		min-height: 600px;
	}

	.intro-content h1 {
		font-size: 3.8rem;
		margin-bottom: .9rem;
	}

	.intro-content h5 {
		font-size: 1.4rem;
		letter-spacing: .15rem;
	}

	.intro-content .modal a {
		width: 66px;
		height: 66px;
		margin-bottom: 1.5rem;
	}
}

@media only screen and (max-width:400px) {
	#intro {
		min-height: 550px;
	}

	.intro-content h1 {
		font-size: 3.4rem;
	}

	.intro-content h5 {
		font-size: 1.4rem;
		letter-spacing: .15rem;
	}

	.intro-content .modal a {
		width: 60px;
		height: 60px;
	}
}

#intro {
	background: #14181E url("./images/intro-bg.png") no-repeat center;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	background-size: cover;
	width: 100%;
	height: 100%;
	min-height: 720px;
	display: table;
	position: relative;
}

.shadow-overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: -moz-linear-gradient(top, rgba(0, 0, 0, 0.45) 0%, rgba(0, 0, 0, 0.25) 25%, transparent 56%);
	background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.45) 0%, rgba(0, 0, 0, 0.25) 25%, transparent 56%);
	background: linear-gradient(to bottom, rgba(0, 0, 0, 0.45) 0%, rgba(0, 0, 0, 0.25) 25%, transparent 56%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#73000000', endColorstr='#00000000', GradientType=0);
}

.intro-content {
	display: table-cell;
	vertical-align: middle;
	text-align: left;
	-webkit-transform: translateY(-1.2rem);
	-ms-transform: translateY(-1.2rem);
	transform: translateY(-1.2rem);
}

.intro-content h1 {
	font-weight: 900;
	font-size: 36px;
	text-transform: uppercase;
	color: #FFFFFF;
	margin-left: 10%;
	letter-spacing: 0.5px;
	text-shadow: 0 0 1px #000;

}

.intro-content h5 {
	font-weight: 400;
	font-size: 16px;
	line-height: 19px;
	margin-left: 10%;
	color: #FFFFFF;
	text-transform: none !important;
	text-shadow: 0 0 1px #000;
}

.intro-content .modal a {
	display: inline-block;
	width: 90px;
	height: 90px;
	margin-bottom: 4.2rem;
	outline: none;
}

.intro-content .button {
	background-color: #304cff;
	display: flex;
	margin-left: 10%;
	margin-top: 20px;
	border-radius: none;
	color: #ffffff;
	width: 100px;
	height: 30px;
	line-height: 30px;
	font-family: Arial;
	font-size: 15px;
	padding: 16px 31px;
	text-decoration: none;
}

@media only screen and (max-width: 2000px) {

	.process-content .right-side,
	.process-content .left-side {
		padding: 0 3% 24px;
		width: 50%;
		height: 45%;
		margin-top: 10%;
	}

	.process-content .image-part {
		width: 50%;
		height: 45%;
	}
}

@media only screen and (max-width:1024px) {

	.process-content .right-side,
	.process-content .left-side {
		padding: 0 3% 24px;
		width: 50%;
		height: 45%;
	}

	.process-content .image-part {
		width: 50%;
		height: 45%;
	}
}

@media only screen and (max-width:768px) {
	#process {
		padding-bottom: 27rem;
		width: 50%;
		height: 45%;
	}

	.process-content .right-side,
	.process-content .left-side {
		position: static;
		width: 50%;
		height: 45%;
		max-width: 480px;
		font-size: 1.6rem;
		line-height: 3rem;
		margin-left: auto;
		margin-right: auto;
		padding-bottom: 0;
	}

	.process-content .image-part {
		width: 50%;
		height: 45%;
		position: absolute;
		bottom: -280px;
		left: 50%;
		margin-left: -150px;
	}
}

@media only screen and (max-width:400px) {
	#process {
		width: 50%;
		height: 45%;
		padding-bottom: 21rem;
	}

	.process-content .image-part {
		width: 50%;
		height: 45%;
		bottom: -235px;
		margin-left: -115px;
	}
}

#process {
	background: #FFFFFF;
	padding-top: 12rem;
	padding-bottom: 50rem;
	overflow: hidden;
}

.process-content {
	position: relative;
	text-align: center;
	display: flex;
}

.process-content .right-side,
.process-content .left-side,
.process-content .image-part {
	width: 33.33333%;
	font-size: 15px;
	line-height: 27px;
}

.process-content .right-side,
.process-content .left-side {
	padding: 0 6% 24px;
	top: 0;
}

.process-content .right-side {
	right: 0;
}

.process-content .left-side {
	left: 0;
}

.process-content .image-part {
	margin: 0 auto;
	background-image: url("./images/appdesign.png");
	background-repeat: no-repeat;
	background-position: center;
	-webkit-background-size: contain;
	-moz-background-size: contain;
	background-size: contain;
	width: 100%;
	height: 100%;
}

#testimonials {
	background: #b6b6b6;
	padding-top: 10.2rem;
	padding-bottom: 22rem;
	position: relative;
	min-height: 20.4rem;
	width: 100%;
	overflow: hidden;
	text-align: center;
	margin-top: 20px;

}

#testimonials .flex-container {
	width: 80%;
	max-width: 250px;
	margin: 0 auto;
}

#testimonial-slider {
	margin: 0 0 3rem 0;
	position: relative;
}

#testimonial-slider p {
	font-family: "raleway-medium", sans-serif;
	font-size: 2.5rem;
	line-height: 1.8;
	color: #8d8d8d;
}

#testimonial-slider p::before,
#testimonial-slider p::after {
	display: inline;
	font-family: Arial, sans-serif;
	color: #c7c7c7;
	font-size: 6rem;
	line-height: 0;
	vertical-align: bottom;
	position: relative;
	top: -1.2rem;
}

#testimonial-slider p::before {
	margin-right: .5rem;
	content: '\201C';
}

#testimonial-slider p::after {
	margin-left: .5rem;
	content: '\201D';
}

#testimonial-slider .testimonial-author .author-info {
	font-family: 'Mardoto';
	font-style: normal;
	font-weight: 900;
	font-size: 20px;
	line-height: 138.69%;
	text-transform: uppercase;
	color: #0079C0;
}

#testimonial-slider .testimonial-author .author-info span {
	display: block;
	font-family: "raleway-semibold", sans-serif;
	font-size: 1.4rem;
	line-height: 2.1rem;
	letter-spacing: 0;
	text-transform: none;
	color: rgba(0, 0, 0, 0.4);
	margin-bottom: 5px;
	margin-top: 5px;
}

.map {
	display: flex;
	width: 500px;
	height: 250px;
	left: -110px;
	top: 90%;
	margin-top: 40px;
	margin-left: -45%;
}

.footer-main .footer-logo {
	width: 162px;
	height: 39px;
	margin: -.6rem 0 2.1rem -.3rem;
	display: block;
	background: url("./images/logo.png");
	background-size: 30px 30px;
}

.footer-bottom {
	margin-top: 1rem;
	text-align: center;
}

.light {
	font-weight: bold;
	color: #2e2e2e;
}